import React, { useContext, useEffect, useState } from "react";
import {songContext} from "../../context/songContext.js"
import "./styles/fullscreen.css"

export default function FullScreen(props){
    const songObj = useContext(songContext)
    const [progVal, setProgVal] = useState(0)
    const [value, setValue] = useState(0)
    const [anime, setAnime] = useState("open")
    function seek(e)
    {
        const percent = e.target.value;
        setProgVal(percent)
        setValue(percent)
        songObj.audio.currentTime = (percent/100)*songObj.audio.duration;
    }
    useEffect(()=>{
        songObj.audio.addEventListener("timeupdate", (e)=>{
            const duration = (songObj.audio.currentTime/songObj.audio.duration)*100
            setProgVal(duration)
            setValue(duration)
        })
    }, [])
    function next(){
        const nextSong = songObj.getNextSong()
        if(nextSong)
        {
          songObj.playSong(nextSong)
        }
      }
      
      function previous(){
        const previousSong = songObj.getPreviousSong()
        if(previousSong)
        {
          songObj.playSong(previousSong)
        }
      }
    return (
        <section id = "fullscreen" className = {"fullscreen-"+anime}>
            <div className = "fullscreen-box" style = {{backgroundImage: `url('${songObj.getCurrentSong()?.src.imageURL}')`}}>
                <div className = "fullscreen-container">
                    <div className = "fullscreen-header">
                        <div className = "fullscreen-button go-back" onClick = {()=>{
                            setAnime("close")
                            setTimeout(()=>{
                                props.setIsHidden(true)
                            }, 150)
                            }
                            }>
                            <span className = "material-icons">
                                arrow_back_ios
                            </span>
                        </div>
                    </div>
                    <div className = "fullscreen-main" >
                        <div className = "fullscreen-btns">
                            <div className ="fullscreen-prevBtn" onClick = {previous}>
                                <span className = "material-icons">
                                    skip_previous
                                </span>
                            </div>
                            <div className = "fullscreen-playBtn">
                            {
                                        songObj.audio.paused? 
                                        <span onClick = {()=>songObj.play()}  className = "material-icons">play_arrow</span>
                                        :
                                        <span onClick = {()=>{songObj.stop()}} className = "material-icons">pause</span>
                                    }
                            </div>
                            <div className ="fullscreen-nextBtn" onClick = {next}>
                                <span className = "material-icons">
                                    skip_next
                                </span>
                            </div>

                        </div>

                        
                    </div>
                    <div className = "fullscreen-footer">
                        <div className = "fullscreen-track-progress">
                            <div className ="fullscreen-track-progress-holder">
                                <div className = "fullscreen-track-progress-meter" style = {{width: progVal + "%"}}/>
                                <input  id = "fullscreen-meter" type = "range" onChange = {seek} value = {value}/>
                            </div>
                            
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}