import React, { useEffect } from "react"
import { NavLink, useHistory } from "react-router-dom";
import { useState } from "react"
import {MoosaxAuth} from "../functions/moosax-auth.js"
import Loader from "react-loader-spinner"
import "../../styles/dropdown.css"

function DropDown(props){
    const [theme, setTheme] = useState()
    const [uid, setUid] = useState()
    const [isLoading, setIsloading] = useState(true)
    const auth = new MoosaxAuth()
    const history = useHistory()
    useEffect(()=>{
        auth.getUserId().then((uid)=>{setUid(uid)}).finally(()=>setIsloading(false))
    }, [])
    
    return(
        <>

                    <div className = "loader drop-loader">
                       
                    </div>
                    <div className = "dropdown-wrapper">
                        <div className = "dropdown">
                            <div className = "dropdown-box">
                                
                                    {isLoading?<Loader type = "TailSpin" color = "#fff"/>:(
                                        <div className = "dropdown-lists">
                                            <ul>
                                                <NavLink to = {"/u/"+uid+"/music"}>
                                                    <li>Profile</li>
                                                </NavLink>
                                                <li>#</li>
                                                <li>#</li>
                                                <li>#</li>
                                                <li onClick = {()=>setTheme("dark")}>Dark Mode</li>
                                                <li onClick = {()=>auth.signOut().then(()=>history.push("/login"))}>Sign Out</li>
                                            </ul>
                                </div>
                                    )
                                    }
                               
                                
                            </div>
                        </div>
                    </div>
                
            
        </>
    )
}

export default DropDown